*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
body{
    font-family: sans-serif;
}
html{
    font-size: 62.5%;
}
.books{
    text-align: center;
    min-height: 100vh;
    background-color: rgb(221, 222, 222);
}
.books h1{
    padding:6rem 0;
    font-size: 3rem;
    text-transform: uppercase;
}

.book-wrapper{
    margin:1.5rem auto 0;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

@media screen and (min-width:992px){
    .books  h1{
        font-size: 4.5rem;
        padding:6rem 0 3rem;
    }
}

