.book{
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: white;
    padding: 3rem 7rem;
    border-radius: 1rem;
    max-width: 90%;
    margin: 2rem;
}

.book img{
   height: 22rem;
   width: 17rem;
   
   display: block;
   margin: 0 auto;
    
}

.book h2{
   width: 100%;
    margin: 1.9rem 0 1rem;
    font-size: 2rem;
}

.book p{
    width:100%;
    font-size: 1.8rem;
    color:rgb(116, 115, 115)
}

@media screen and (min-width:440px){
    
    .book{
         width: 70%;
    }
}

@media screen and (min-width:800px){
    
    .book{
        width: 40%;
    }
}
@media screen and (min-width:1000px){
    
    .book{
        width: 29%;
    }
}


@media screen and (min-width:1300px){
    
    .book{
        width: 25%;
    }
}


